import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { IdentifiedPaymentOrder } from '@backbase/data-ang/payment-order';
import { LoadMoreParams } from '@backbase/ui-ang/load-button';
import { UpcomingPaymentsGroup } from '../../models/payments.model';

@Component({
  selector: 'bb-upcoming-payments-list',
  templateUrl: 'upcoming-payments-list.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class UpcomingPaymentsListComponent {
  /**
   * The upcoming payment list.
   */
  @Input() upcomingPayments?: UpcomingPaymentsGroup[] = [];
  /**
   * Total count for payment.
   * Default value is `0`.
   */
  @Input() totalCount = 0;
  /**
   * Indicator for load more loading state.
   * Default value is `false`.
   */
  @Input() loading = false;
  /**
   * From parameter for load more payments request.
   * Default value is `0`.
   */
  @Input() loadMoreFrom = 0;
  /**
   * Size parameter for load more payments request.
   * Default value is `0`.
   */
  @Input() loadMoreSize = 0;
  /**
   * changes will be triggered on paymentItemClick event emit.
   */
  @Output() paymentItemClick = new EventEmitter<IdentifiedPaymentOrder>();
  /**
   * changes will be triggered on loadMoreParam event emit.
   */
  @Output() loadMoreParam = new EventEmitter<LoadMoreParams>();
  /**
   *On cancel event emit.
   */
  @Output() cancelPayment = new EventEmitter<IdentifiedPaymentOrder>();

  loadMoreParamChange(event: LoadMoreParams) {
    this.loadMoreFrom = event.from;
    this.loadMoreParam.emit(event);
  }

  onPaymentItemClick(paymentItem: IdentifiedPaymentOrder) {
    this.paymentItemClick.emit(paymentItem);
  }

  trackPaymentsGroup(_: number, paymentsGroup?: UpcomingPaymentsGroup): string | undefined {
    return paymentsGroup ? paymentsGroup.executionDate : undefined;
  }

  onCancelPayment(payment: IdentifiedPaymentOrder) {
    this.cancelPayment.emit(payment);
  }

  get hasMore(): boolean {
    const currentCount = this.loadMoreFrom * this.loadMoreSize + this.loadMoreSize;
    return currentCount < this.totalCount;
  }
}
